.aproach {
  color: white;
  width: 362px;

  &__subtitle {
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    text-transform: uppercase;
    margin-bottom: 16px;
  }

  &__title {
    font-weight: 600;
    font-size: 32px;
    margin-bottom: 32px;
  }

  &__photo {
    position: absolute;
    bottom: 0;
    top: 296px;
    right: -100px;
    background-image: url(../images/aproach/photo.jpg);
    background-position: center;
    background-repeat: no-repeat;
    width: 687px;
    height: 680px;
    border-radius: 200px 0 0;
  }

  &__text {
    font-family: "Open Sans", sans-serif;
    font-size: 15px;
    margin-left: 98px;
  }

  &__item {
    height: 64px;
    margin-bottom: 64px;
    position: relative;
  }

  &__icon {
    position: absolute;
    display: inline-block;
    width: 64px;
    height: 64px;
    background-color: white;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;

    &--first {
      background-image: url(../images/aproach/1.png);
    }

    &--second {
      background-image: url(../images/aproach/2.png);
    }

    &--third {
      background-image: url(../images/aproach/3.png);
    }
  }

}
